export interface FileUploadResponse {
  fileUrl: string;
  fileName: string;
  fileSize: number;
  fileType: string;
  uploadedAt: string;
}

export type FileCategory = "image" | "video" | "pdf" | "signature";

export interface UploadProgressCallback {
  (progress: number): void;
}

/**
 * Ø±ÙØ¹ Ù…Ù„Ù Ø¥Ù„Ù‰ Ø§Ù„Ø®Ø§Ø¯Ù…
 * @param file Ø§Ù„Ù…Ù„Ù Ø§Ù„Ù…Ø±Ø§Ø¯ Ø±ÙØ¹Ù‡
 * @param type Ù†ÙˆØ¹ Ø§Ù„Ù…Ù„Ù (image, video, pdf, signature)
 * @param onProgress callback Ù„ØªØªØ¨Ø¹ Ø§Ù„ØªÙ‚Ø¯Ù…
 * @returns Ù…Ø¹Ù„ÙˆÙ…Ø§Øª Ø§Ù„Ù…Ù„Ù Ø§Ù„Ù…Ø±ÙÙˆØ¹
 */
export async function uploadFile(
  file: File,
  type: FileCategory,
  onProgress?: UploadProgressCallback
): Promise<FileUploadResponse> {
  const formData = new FormData();
  formData.append("file", file);

  // Ø§Ø³ØªØ®Ø¯Ø§Ù… XMLHttpRequest Ù„Ù„Ø­ØµÙˆÙ„ Ø¹Ù„Ù‰ progress
  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest();

    xhr.upload.addEventListener("progress", (event) => {
      if (event.lengthComputable && onProgress) {
        const progress = Math.round((event.loaded / event.total) * 100);
        onProgress(progress);
      }
    });

    xhr.addEventListener("load", () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        try {
          const response: ApiResponse<FileUploadResponse> = JSON.parse(
            xhr.responseText
          );
          if (response.success && response.data) {
            resolve(response.data);
          } else {
            reject(new Error(response.message || "ÙØ´Ù„ Ø±ÙØ¹ Ø§Ù„Ù…Ù„Ù"));
          }
        } catch {
          reject(new Error("Ø®Ø·Ø£ ÙÙŠ ØªØ­Ù„ÙŠÙ„ Ø§Ù„Ø§Ø³ØªØ¬Ø§Ø¨Ø©"));
        }
      } else {
        try {
          const errorResponse = JSON.parse(xhr.responseText);
          reject(new Error(errorResponse.message || `Ø®Ø·Ø£ HTTP: ${xhr.status}`));
        } catch {
          reject(new Error(`Ø®Ø·Ø£ HTTP: ${xhr.status}`));
        }
      }
    });

    xhr.addEventListener("error", () => {
      reject(new Error("ÙØ´Ù„ Ø§Ù„Ø§ØªØµØ§Ù„ Ø¨Ø§Ù„Ø®Ø§Ø¯Ù…"));
    });

    xhr.addEventListener("abort", () => {
      reject(new Error("ØªÙ… Ø¥Ù„ØºØ§Ø¡ Ø§Ù„Ø±ÙØ¹"));
    });

    // ÙØªØ­ Ø§Ù„Ø§ØªØµØ§Ù„
    // NEXT_PUBLIC_API_URL ÙŠØ­ØªÙˆÙŠ Ø¹Ù„Ù‰ /api Ø¨Ø§Ù„ÙØ¹Ù„ (Ù…Ø«Ù„: http://localhost:5250/api)
    const baseUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5250/api";
    xhr.open("POST", `${baseUrl}/Files/upload?type=${type}`);

    // Ø¥Ø¶Ø§ÙØ© credentials Ø¥Ø°Ø§ Ù„Ø²Ù… Ø§Ù„Ø£Ù…Ø±
    xhr.withCredentials = true;

    xhr.send(formData);
  });
}

/**
 * Ø±ÙØ¹ Ù…Ù„Ù Ø¨Ø§Ø³ØªØ®Ø¯Ø§Ù… fetch (Ø¨Ø¯ÙˆÙ† progress)
 * @param file Ø§Ù„Ù…Ù„Ù
 * @param type Ù†ÙˆØ¹ Ø§Ù„Ù…Ù„Ù
 */
export async function uploadFileSimple(
  file: File,
  type: FileCategory
): Promise<FileUploadResponse> {
  const formData = new FormData();
  formData.append("file", file);

  const response = await apiClient.post<ApiResponse<FileUploadResponse>>(
    `/Files/upload?type=${type}`,
    formData,
    {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    }
  );

  if (!response.data.success || !response.data.data) {
    throw new Error(response.data.message || "ÙØ´Ù„ Ø±ÙØ¹ Ø§Ù„Ù…Ù„Ù");
  }

  return response.data.data;
}

/**
 * ØªØ­ÙˆÙŠÙ„ Base64 Ø¥Ù„Ù‰ File
 * Ù…ÙÙŠØ¯ Ù„Ù„ØªÙˆÙ‚ÙŠØ¹Ø§Øª
 */
export function base64ToFile(
  base64: string,
  fileName: string,
  mimeType: string
): File {
  const byteString = atob(base64.split(",")[1]);
  const ab = new ArrayBuffer(byteString.length);
  const ia = new Uint8Array(ab);

  for (let i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }

  return new File([ab], fileName, { type: mimeType });
}

/**
 * Ø§Ù„Ø­ØµÙˆÙ„ Ø¹Ù„Ù‰ URL Ø§Ù„ÙƒØ§Ù…Ù„ Ù„Ù„Ù…Ù„Ù
 * Ø§Ù„Ù…Ù„ÙØ§Øª Ø§Ù„Ø«Ø§Ø¨ØªØ© ØªÙØ®Ø¯Ù… Ù…Ù† Ø§Ù„Ø¬Ø°Ø± ÙˆÙ„ÙŠØ³ Ù…Ù† /api
 */
export function getFullFileUrl(fileUrl: string): string {
  if (!fileUrl) return "";
  if (fileUrl.startsWith("http") || fileUrl.startsWith("data:")) {
    return fileUrl;
  }
  // Ø¥Ø²Ø§Ù„Ø© /api Ù…Ù† Ø§Ù„Ù…Ø³Ø§Ø± Ù„Ø£Ù† Ø§Ù„Ù…Ù„ÙØ§Øª Ø§Ù„Ø«Ø§Ø¨ØªØ© ØªÙØ®Ø¯Ù… Ù…Ù† Ø§Ù„Ø¬Ø°Ø±
  let baseUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5250";
  // Ø¥Ø°Ø§ ÙƒØ§Ù† baseUrl ÙŠÙ†ØªÙ‡ÙŠ Ø¨Ù€ /apiØŒ Ù†Ø²ÙŠÙ„Ù‡
  if (baseUrl.endsWith("/api")) {
    baseUrl = baseUrl.slice(0, -4);
  }
  return `${baseUrl}${fileUrl}`;
}

